@import "../../style/variables.scss";
.auth {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;

  &__top {
    text-align: center;
    margin-bottom: 50px;
    h2 {
      font-size: 28px;
      margin-bottom: 5px;
    }
    p {
      font-size: 18px;
      line-height: 21px;
      letter-spacing: 0.1px;
      opacity: 0.5;
    }
  }
  &__content {
    width: 350px;
    padding: 10px;
    border: 2px solid $border-color;
  }

  &__register-link {
    text-align: center;
    display: block;
    font-size: 16px;
    letter-spacing: 0.1px;
    color: $font-color-secondary;
  }
}
